.object-income {
    margin-bottom: 70px;
}

.object-income__title {
    margin: 0;
    margin-bottom: 20px;

    font-size: 24px;
    font-weight: 400;
}

.object-income__content {
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;
}

.object-income__chart {
    flex: 1 1 auto;
    position: relative;
    height: 300px;
    margin-right: 40px;
    max-width: 600px;

    #income-chart {
        min-width: 300px;
        height: 100%;
        width: auto;

        .simplebar-track {
            width: 100%;

            background-color: rgba(233, 233, 233, 0.582);

            &.vertical {
                display: none;
            }
        }

        .simplebar-scrollbar {
            width: 4px;
            right: 0;

            &::before {
                opacity: 1;
                background-color: $themecolor;
            }
        }

        .simplebar-track.horizontal {
            height: 7px;
            border-radius: 4px;
        }

        .horizontal.simplebar-track .simplebar-scrollbar {
            height: 7px;
            border-radius: 4px;
            top: 0; 
        }
    }

    .highcharts-credits {
        display: none;
    }
}

.object-income__legend {
    width: 140px;
    flex: none;

    font-size: 12px;
}

.object-income__legend-item {
    position: relative;
    margin-bottom: 20px;

    padding-left: 27px;

    &::before {
        content: '';
        position: absolute;
        width: 18px;
        height: 18px;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    &:nth-child(1)::before {
        background-color: #ffd729;
    }

    &:nth-child(2)::before {
        background-color: #3c7bd8;
    }
}

.object-income__link {
    position: relative;
    font-size: 14px;
    color: #3c7bd8;
    text-decoration: none;

    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 0;
        left: 0;
        bottom: -3px;
        border-bottom: 1px dashed #3c7bd8;

        transition: color 300ms ease;
    }

    &:hover,
    &:active,
    &:focus {
        text-decoration: none;

        color: darken($color: #3c7bd8, $amount: 20%);
    }
}

@media (max-width: 1040px) {
    .object-income__content {
        display: block;
        margin-bottom: 20px;
    }

    .object-income__chart {
        width: 100%;
        margin-right: 0;
    }

    .object-income__legend {
        margin: 20px auto 0;
        width: 280px;
        display: flex;
        justify-content: space-between;
    }
}

@media (max-width: 480px) {
    .object-income {
        margin-bottom: 25px;
    }

    .object-income__title {
        font-size: 20px;
    }
}
